/* ----------------------------------- COLORS ----------------------------------- */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

:root {
  font-family: "Red Hat Display", sans-serif;
  font-size: 16px;
  text-align: center;
}

body {
  background-color: hsl(225deg, 100%, 94%);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.bg {
  z-index: -1;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  background-image: url(../img/pattern-background-mobile.svg);
  background-color: #e6ecff;
  background-size: contain;
  background-repeat: no-repeat;
  top: -50px;
}

.order-card {
  min-width: 320px;
  max-width: 450px;
  width: 90%;
  min-height: 565px;
  background-color: #fff;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 5px 15px 15px rgba(0, 0, 0, 0.05);
}
.order-card .image {
  width: 100%;
  height: 160px;
  background-image: url(../img/illustration-hero.svg);
  background-size: cover;
}
.order-card .order-content-wrapper {
  padding: 0 1.5rem;
}
.order-card .order-content-wrapper h1 {
  font-size: 1.4rem;
  font-weight: 900;
  color: hsl(223deg, 47%, 23%);
  margin: 1.8rem 0 1.3rem;
}
.order-card .order-content-wrapper .order-description {
  line-height: 1.6rem;
  color: hsl(224deg, 23%, 55%);
  font-size: 0.95rem;
  padding: 0 0.5rem;
  margin-bottom: 1.3rem;
}
.order-card .order-content-wrapper .order-details {
  width: 100%;
  background-color: hsl(225deg, 100%, 98%);
  border-radius: 10px;
  display: flex;
  align-items: center;
  padding: 1rem;
  margin-bottom: 1.4rem;
}
.order-card .order-content-wrapper .order-details .plan-and-cost {
  margin-left: 1.2rem;
  margin-right: 2.5rem;
  font-size: 0.9rem;
}
.order-card .order-content-wrapper .order-details .plan-and-cost p:nth-child(1) {
  font-weight: 900;
  margin-bottom: 0.1rem;
}
.order-card .order-content-wrapper .order-details .plan-and-cost p:nth-child(2) {
  font-weight: 500;
  color: hsl(224deg, 23%, 55%);
}
.order-card .order-content-wrapper .order-details a {
  font-weight: 700;
  font-size: 0.8rem;
  color: hsl(245deg, 75%, 52%);
  cursor: pointer;
}
.order-card .order-content-wrapper .order-details a:hover {
  color: hsl(245deg, 83%, 68%);
  text-decoration: none;
}
.order-card .order-content-wrapper .btn, .order-card .order-content-wrapper .order-cancel {
  display: block;
  text-decoration: none;
  font-size: 0.9rem;
}
.order-card .order-content-wrapper .btn {
  background-color: hsl(245deg, 75%, 52%);
  color: #fff;
  font-weight: 700;
  padding: 1rem 0;
  border-radius: 10px;
  margin-bottom: 1.4rem;
  cursor: pointer;
  transition: background-color 0.2s ease-in-out;
  box-shadow: 0 20px 13px 0px rgba(56, 41, 224, 0.15);
}
.order-card .order-content-wrapper .btn:hover {
  background-color: hsl(245deg, 83%, 68%);
}
.order-card .order-content-wrapper .order-cancel {
  margin: 0 auto;
  width: fit-content;
  color: hsl(224deg, 23%, 55%);
  font-weight: 900;
}
.order-card .order-content-wrapper .order-cancel:hover {
  color: hsl(223deg, 47%, 23%);
}

@media screen and (min-width: 768px) {
  .bg {
    background-image: url(../img/pattern-background-desktop.svg);
  }
  .order-card {
    height: 700px;
  }
  .order-card .image {
    height: 220px;
  }
  .order-card .order-content-wrapper {
    padding: 0 3rem;
  }
  .order-card .order-content-wrapper h1 {
    font-size: 1.8rem;
    margin: 2.9rem 0 1.1rem;
  }
  .order-card .order-content-wrapper .order-details {
    padding: 1.5rem;
    margin-bottom: 2rem;
  }
  .order-card .order-content-wrapper .order-details .plan-and-cost {
    font-size: 1rem;
    margin-right: 5.8rem;
  }
  .order-card .order-content-wrapper .order-details .plan-and-cost p:nth-child(1) {
    margin-bottom: 0.2rem;
  }
  .order-card .order-content-wrapper .order-details a {
    font-size: 0.9rem;
  }
  .order-card .order-content-wrapper .btn, .order-card .order-content-wrapper .order-cancel {
    font-size: 1rem;
  }
  .order-card .order-content-wrapper .btn {
    padding: 0.9rem 0;
    margin-bottom: 2rem;
  }
}